import React from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { BotIcon, LightbulbIcon, FileText, ExternalLink, Sparkles, AlertCircle } from 'lucide-react';
import Link from 'next/link';
import { cleanDocumentTitle } from '@/lib/utils/document-utils';

interface QwenResponseDisplayProps {
  generatedResponse: string;
 generationType: string;
 sources?: Array<{ title: string; documentId: string; confidence: number }>;
 isLoading?: boolean;
  error?: string;
  relevantDocumentUrl?: string; // Added for the clickable document link
}

const QwenResponseDisplay: React.FC<QwenResponseDisplayProps> = ({
  generatedResponse,
  generationType,
  sources,
  isLoading,
  error,
  relevantDocumentUrl // Added to props
}) => {
 if (isLoading) {
    return (
      <Card className="mb-6 border-0 shadow-lg bg-gradient-to-br from-blue-50/50 to-purple-50/50 dark:from-blue-950/20 dark:to-purple-950/20 overflow-hidden">
        <CardHeader className="pb-3">
          <CardTitle className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-500 to-purple-500 flex items-center justify-center">
              <Sparkles className="w-5 h-5 text-white animate-pulse" />
            </div>
            <div>
              <div className="text-lg font-semibold">AI is analyzing your documents</div>
              <div className="text-sm text-muted-foreground font-normal">Finding the most relevant information...</div>
            </div>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <div className="space-y-3">
            <div className="h-4 bg-gradient-to-r from-slate-200 to-slate-100 dark:from-slate-700 dark:to-slate-800 rounded-full animate-pulse w-3/4"></div>
            <div className="h-4 bg-gradient-to-r from-slate-200 to-slate-100 dark:from-slate-700 dark:to-slate-800 rounded-full animate-pulse"></div>
            <div className="h-4 bg-gradient-to-r from-slate-200 to-slate-100 dark:from-slate-700 dark:to-slate-800 rounded-full animate-pulse w-5/6"></div>
            <div className="h-4 bg-gradient-to-r from-slate-200 to-slate-100 dark:from-slate-700 dark:to-slate-800 rounded-full animate-pulse w-2/3"></div>
          </div>
          {sources && sources.length > 0 && (
            <div className="mt-6 pt-4 border-t border-slate-200/50 dark:border-slate-700/50">
              <h4 className="flex items-center gap-2 font-medium mb-3 text-sm text-muted-foreground">
                <FileText className="w-4 h-4" />
                Sources being analyzed
              </h4>
              <div className="flex flex-wrap gap-2">
                {sources.slice(0, 3).map((source, index) => (
                  <div key={index} className="h-6 bg-slate-200/50 dark:bg-slate-700/50 rounded-full px-3 animate-pulse w-32"></div>
                ))}
              </div>
            </div>
          )}
        </CardContent>
      </Card>
    );
  }

  if (error) {
    return (
      <Card className="mb-6 border-red-200/50 bg-red-50/50 dark:bg-red-950/10 dark:border-red-900/30">
        <CardHeader>
          <CardTitle className="flex items-center gap-3 text-red-600 dark:text-red-400">
            <div className="w-10 h-10 rounded-xl bg-red-100 dark:bg-red-900/50 flex items-center justify-center">
              <AlertCircle className="w-5 h-5" />
            </div>
            <div>
              <div className="text-lg font-semibold">Unable to generate response</div>
              <div className="text-sm text-red-500/80 dark:text-red-400/70 font-normal">Please try again or refine your search</div>
            </div>
          </CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-red-600/80 dark:text-red-400/80 text-sm">{error}</p>
        </CardContent>
      </Card>
    );
  }

  if (!generatedResponse) {
    return null;
  }

 return (
    <Card className="mb-6 border-0 shadow-lg bg-gradient-to-br from-blue-50/80 to-purple-50/80 dark:from-blue-950/30 dark:to-purple-950/30 overflow-hidden">
      <CardHeader className="pb-3">
        <CardTitle className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-blue-500 to-purple-500 flex items-center justify-center shrink-0">
            <Sparkles className="w-5 h-5 text-white" />
          </div>
          <div>
            <div className="text-lg font-semibold">AI-Generated Answer</div>
            <div className="text-sm text-muted-foreground font-normal">Based on your institutional documents</div>
          </div>
        </CardTitle>
      </CardHeader>
      <CardContent>
        <div className="prose prose-slate dark:prose-invert max-w-none">
          <div className="text-foreground leading-relaxed">
            {generatedResponse.split('\n').map((line, i) => {
              // Check if the line contains a list item (starts with * or - or numbered list)
              const trimmedLine = line.trim();
              if (trimmedLine.startsWith('* **') || trimmedLine.startsWith('- **')) {
                // Handle nested bullet list items like "* **Name of Person**"
                const match = trimmedLine.match(/^\s*[*-]\s*\*\*(.+?)\*\*(.*)/);
                if (match) {
                  return (
                    <div key={i} className="ml-4 my-1.5 flex items-start gap-2">
                      <span className="w-1.5 h-1.5 rounded-full bg-blue-500 mt-2 shrink-0"></span>
                      <span><span className="font-semibold">{match[1]}</span>{match[2]}</span>
                    </div>
                  );
                }
                return (
                  <div key={i} className="ml-4 my-1.5 flex items-start gap-2">
                    <span className="w-1.5 h-1.5 rounded-full bg-blue-500 mt-2 shrink-0"></span>
                    <span>{trimmedLine.substring(2).trim()}</span>
                  </div>
                );
              } else if (/^\s*\d+\.\s/.test(trimmedLine)) {
                // Handle numbered lists
                return <div key={i} className="ml-4 my-1.5">{trimmedLine}</div>;
              } else if (trimmedLine.startsWith('**') && trimmedLine.endsWith('**')) {
                // Handle bold headers
                return <div key={i} className="font-bold mt-4 mb-2 text-foreground">{trimmedLine.slice(2, -2)}</div>;
              } else if (trimmedLine === '') {
                // Handle empty lines
                return <div key={i} className="my-3"></div>;
              } else {
                // Handle regular text
                return <div key={i} className="my-1">{line}</div>;
              }
            })}
          </div>
          
          {sources && sources.length > 0 && (
            <div className="mt-6 pt-5 border-t border-slate-200/50 dark:border-slate-700/50">
              <h4 className="flex items-center gap-2 font-semibold mb-4 text-sm">
                <FileText className="w-4 h-4 text-muted-foreground" />
                Source Documents
              </h4>
              <div className="grid gap-3">
                {sources.map((source, index) => (
                  <div key={index} className="flex items-center gap-3 p-3 rounded-xl bg-white/60 dark:bg-slate-800/60 border border-slate-200/50 dark:border-slate-700/50 hover:border-blue-300 dark:hover:border-blue-700 transition-colors group">
                    <span className="text-xs font-medium bg-blue-100 dark:bg-blue-900/50 text-blue-700 dark:text-blue-300 rounded-full w-6 h-6 flex items-center justify-center shrink-0">
                      {index + 1}
                    </span>
                    <div className="flex-1 min-w-0">
                      {source.documentId && source.documentId !== 'undefined' && !source.documentId.includes('undefined') && !/\.(pdf|docx?|xlsx?|pptx?|jpg|jpeg|png|gif|bmp|tiff|webp)$/i.test(source.documentId) ? (
                        <Link href={`/repository/preview/${source.documentId}`} target="_blank" rel="noopener noreferrer" className="font-medium text-foreground hover:text-blue-600 dark:hover:text-blue-400 transition-colors truncate block">
                          {cleanDocumentTitle(source.title)}
                        </Link>
                      ) : (
                        <div className="font-medium truncate">{cleanDocumentTitle(source.title)}</div>
                      )}
                      {source.confidence && (
                        <div className="flex items-center gap-1 mt-1">
                          <div className="h-1.5 w-16 bg-slate-200 dark:bg-slate-700 rounded-full overflow-hidden">
                            <div 
                              className="h-full bg-gradient-to-r from-blue-500 to-purple-500 rounded-full" 
                              style={{ width: `${source.confidence * 100}%` }}
                            ></div>
                          </div>
                          <span className="text-xs text-muted-foreground">{(source.confidence * 100).toFixed(0)}% match</span>
                        </div>
                      )}
                    </div>
                    {source.documentId && source.documentId !== 'undefined' && !source.documentId.includes('undefined') && !/\.(pdf|docx?|xlsx?|pptx?|jpg|jpeg|png|gif|bmp|tiff|webp)$/i.test(source.documentId) && (
                      <Link href={`/repository/preview/${source.documentId}`} target="_blank" rel="noopener noreferrer" className="text-muted-foreground hover:text-blue-600 dark:hover:text-blue-400 transition-colors opacity-0 group-hover:opacity-100">
                        <ExternalLink className="w-4 h-4" />
                        <span className="sr-only">View Document</span>
                      </Link>
                    )}
                  </div>
                ))}
              </div>
            </div>
          )}
          
          {relevantDocumentUrl && (
            <div className="mt-6 pt-5 border-t border-slate-200/50 dark:border-slate-700/50">
              <Link 
                href={relevantDocumentUrl} 
                target="_blank" 
                rel="noopener noreferrer" 
                className="inline-flex items-center gap-3 px-5 py-3 rounded-xl bg-gradient-to-r from-blue-500 to-blue-600 hover:from-blue-600 hover:to-blue-700 text-white font-medium shadow-lg shadow-blue-500/25 transition-all duration-200 hover:scale-[1.02]"
              >
                <FileText className="w-5 h-5" />
                <span>View Source Document</span>
                <ExternalLink className="w-4 h-4" />
              </Link>
            </div>
          )}
        </div>
      </CardContent>
    </Card>
  );
};

export default QwenResponseDisplay;