"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/lib/auth-context"
import { Navbar } from "@/components/navbar"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Badge } from "@/components/ui/badge"
import { SearchIcon, FileText, TrendingUp, BotIcon, Eye, Sparkles, BookOpen, GraduationCap, Users, Lightbulb, ArrowRight, Clock, Star } from "lucide-react"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import Image from "next/image"
import AuthService from '@/lib/services/auth-service';
import { Document } from '@/lib/api/types';
import SuperMapper from '@/lib/utils/super-mapper';
import QwenResponseDisplay from '@/components/qwen-response-display';
import { cleanDocumentTitle } from '@/lib/utils/document-utils';
import { useToast } from '@/components/ui/use-toast';

export default function SearchPage() {
  const { user, isAuthenticated, isLoading } = useAuth()
  const router = useRouter()
  const [searchQuery, setSearchQuery] = useState("")
  const [hasPerformedSearch, setHasPerformedSearch] = useState(false)
  const [searchResults, setSearchResults] = useState<{
    documents: Document[]
  }>({
    documents: [],
  })
  const [generatedResponse, setGeneratedResponse] = useState<string | null>(null);
  const [generationType, setGenerationType] = useState<string>('');
  const [isGenerating, setIsGenerating] = useState<boolean>(false);
  const [loading, setLoading] = useState(false);
  const [sources, setSources] = useState<any[]>([]); // State to store the sources used in the AI response
  const [relevantDocumentUrl, setRelevantDocumentUrl] = useState<string | undefined>(undefined); // State to store the relevant document URL

  useEffect(() => {
    if (!isLoading && !isAuthenticated) {
      router.push("/");
    }
  }, [isAuthenticated, isLoading, router])

  const performSearch = async () => {
    if (searchQuery.trim()) {
      setHasPerformedSearch(true);
      setLoading(true);
      setIsGenerating(true);
      try {
        const token = await AuthService.getAccessToken();
        if (!token) {
          throw new Error('No authentication token found');
        }

        // Always use semantic search with AI generation by default
        const needsGeneration = true; // Always generate AI response
        
        // Build query parameters for the new search API - always use semantic search
        const queryParams = new URLSearchParams();
        queryParams.append('query', searchQuery);
        queryParams.append('useSemantic', 'true'); // Always use semantic search
        queryParams.append('generate', needsGeneration.toString()); // Always generate AI response
        queryParams.append('generationType', 'text-only'); // Default to text-only generation for documents
        
        // Search for documents using the new enhanced search API
        const response = await fetch(`/api/search?${queryParams.toString()}`, {
          headers: {
            'Authorization': `Bearer ${token}`,
            'Content-Type': 'application/json',
          }
        });

        if (!response.ok) {
          // Handle different status codes appropriately
          if (response.status === 500) {
            console.error('Search API internal server error:', response.status, response.statusText);
            // Set empty results and show user-friendly message
            setSearchResults({ documents: [] });
            return;
          } else if (response.status === 401) {
            console.error('Authentication error during search:', response.status);
            await AuthService.logout();
            return;
          } else if (response.status === 403) {
            console.error('Forbidden access during search:', response.status);
            setSearchResults({ documents: [] });
            return;
          } else {
            throw new Error(`Failed to fetch documents: ${response.status} ${response.statusText}`);
          }
        }

        const data = await response.json();
        
        // Extract documents from the enhanced search results
        // Handle both direct document results and enhanced search results with additional metadata
        const documents = data.results.map((result: any) => {
          // If result has a document property, use it; otherwise use the result itself
          return result.document || result;
        });
        
        // Handle generated response if present
        if (data.generatedResponse) {
          setGeneratedResponse(data.generatedResponse);
          setGenerationType(data.generationType || 'semantic'); // Default to semantic
          setSources(data.sources || []); // Set the sources used in the AI response
          setRelevantDocumentUrl(data.relevantDocumentUrl || undefined); // Set the relevant document URL
        } else {
          setGeneratedResponse(null);
          setSources([]); // Clear sources when there's no generated response
          setRelevantDocumentUrl(undefined); // Clear the document URL as well
        }
        
        setSearchResults({
          documents: documents || [],
        });
      } catch (error) {
        console.error('Search error:', error);
        setSearchResults({ documents: [] });
        setGeneratedResponse(null);
        setSources([]); // Clear sources on error as well
        setRelevantDocumentUrl(undefined); // Clear document URL on error as well
      } finally {
        setLoading(false);
        setIsGenerating(false);
      }
    } else {
      setSearchResults({ documents: [] });
      setGeneratedResponse(null);
      setHasPerformedSearch(false);
      setSources([]); // Clear sources when there's no search query
      setRelevantDocumentUrl(undefined); // Clear document URL when there's no search query
    }
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 flex items-center justify-center mx-auto mb-4 overflow-hidden">
            <Image
              src="/LSPULogo.png"
              alt="LSPU Logo"
              width={64}
              height={64}
              className="object-contain animate-spin"
            />
          </div>
          <p className="text-lg text-muted-foreground">Loading search...</p>
        </div>
      </div>
    );
  }

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 flex items-center justify-center mx-auto mb-4 overflow-hidden">
            <Image
              src="/LSPULogo.png"
              alt="LSPU Logo"
              width={64}
              height={64}
              className="object-contain animate-spin"
            />
          </div>
          <p className="text-lg text-muted-foreground">Redirecting to login...</p>
        </div>
      </div>
    );
  }

  // Don't render if user is null but authentication is loading
  if (!user) {
    return (
      <div className="min-h-screen bg-background flex items-center justify-center">
        <div className="text-center">
          <div className="w-16 h-16 flex items-center justify-center mx-auto mb-4 overflow-hidden">
            <Image
              src="/LSPULogo.png"
              alt="LSPU Logo"
              width={64}
              height={64}
              className="object-contain animate-spin"
            />
          </div>
          <p className="text-lg text-muted-foreground">Loading user data...</p>
        </div>
      </div>
    );
  }

  const totalResults = searchResults.documents.length

  const popularSearches = [
    { label: "Research Papers", icon: BookOpen },
    { label: "Curriculum", icon: GraduationCap },
    { label: "Policy Documents", icon: FileText },
    { label: "Extension Programs", icon: Users },
    { label: "Teaching Materials", icon: Lightbulb },
  ]

  const recentSearches = ["Strategic Plan", "Faculty Development", "Accreditation"]

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50/30 to-slate-50 dark:from-slate-950 dark:via-blue-950/20 dark:to-slate-950">
      <Navbar />
      <main className="max-w-5xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {/* Hero Section with Enhanced Search */}
        {!hasPerformedSearch && (
          <div className="relative mb-12">
            {/* Decorative Elements */}
            <div className="absolute inset-0 overflow-hidden pointer-events-none">
              <div className="absolute -top-20 -right-20 w-72 h-72 bg-blue-200/20 dark:bg-blue-500/10 rounded-full blur-3xl animate-float" />
              <div className="absolute -bottom-20 -left-20 w-96 h-96 bg-purple-200/20 dark:bg-purple-500/10 rounded-full blur-3xl animate-float-delayed" />
            </div>

            {/* Main Header */}
            <div className="relative text-center pt-8 pb-6 animate-fade-in">
              <div className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-primary/5 border border-primary/10 mb-6">
                <Sparkles className="w-4 h-4 text-primary" />
                <span className="text-sm font-medium text-primary">Powered by AI</span>
              </div>
              <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold bg-gradient-to-r from-slate-900 via-blue-800 to-slate-900 dark:from-slate-100 dark:via-blue-300 dark:to-slate-100 bg-clip-text text-transparent mb-4">
                Intelligent Search
              </h1>
              <p className="text-lg md:text-xl text-muted-foreground max-w-2xl mx-auto">
                Ask questions or search for documents in English or Filipino. 
                <br className="hidden sm:block" />
                Our AI understands context and finds what you need.
              </p>
            </div>

            {/* Enhanced Search Input */}
            <div className="relative max-w-3xl mx-auto animate-fade-in" style={{ animationDelay: '100ms' }}>
              <div className="relative group">
                <div className="absolute -inset-1 bg-gradient-to-r from-blue-500/20 via-purple-500/20 to-blue-500/20 rounded-2xl blur-lg opacity-0 group-hover:opacity-100 group-focus-within:opacity-100 transition-all duration-500" />
                <div className="relative bg-white dark:bg-slate-900 rounded-2xl shadow-xl border border-slate-200/50 dark:border-slate-700/50 overflow-hidden">
                  <div className="flex items-center gap-3 p-2">
                    <div className="flex items-center justify-center w-12 h-12 rounded-xl bg-gradient-to-br from-blue-500 to-blue-600 text-white shrink-0 ml-2">
                      <SearchIcon className="w-5 h-5" />
                    </div>
                    <Input
                      placeholder="Ask anything about LSPU documents..."
                      value={searchQuery}
                      onChange={(e) => setSearchQuery(e.target.value)}
                      onKeyDown={(e) => {
                        if (e.key === 'Enter') {
                          performSearch();
                        }
                      }}
                      className="flex-1 h-14 text-lg border-0 shadow-none focus-visible:ring-0 bg-transparent placeholder:text-muted-foreground/60"
                    />
                    <Button
                      onClick={performSearch}
                      size="lg"
                      className="h-12 px-6 rounded-xl bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white shadow-lg shadow-blue-500/25 mr-2 transition-all duration-200 hover:scale-105"
                    >
                      <span className="hidden sm:inline mr-2">Search</span>
                      <ArrowRight className="w-4 h-4" />
                    </Button>
                  </div>
                </div>
              </div>

              {/* Keyboard Hint */}
              <div className="flex justify-center mt-4">
                <div className="inline-flex items-center gap-2 text-xs text-muted-foreground">
                  <kbd className="px-2 py-1 bg-slate-100 dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded text-[10px] font-mono">Enter</kbd>
                  <span>to search</span>
                </div>
              </div>
            </div>

            {/* Popular Searches */}
            <div className="mt-10 animate-fade-in" style={{ animationDelay: '200ms' }}>
              <div className="text-center mb-4">
                <span className="text-sm font-medium text-muted-foreground flex items-center justify-center gap-2">
                  <TrendingUp className="w-4 h-4" />
                  Popular Searches
                </span>
              </div>
              <div className="flex flex-wrap justify-center gap-3">
                {popularSearches.map((search, index) => {
                  const Icon = search.icon
                  return (
                    <Button
                      key={index}
                      variant="outline"
                      size="sm"
                      onClick={() => {
                        setSearchQuery(search.label)
                        performSearch()
                      }}
                      className="rounded-full border-slate-200 dark:border-slate-700 hover:bg-primary/5 hover:border-primary/30 hover:text-primary transition-all duration-200 group"
                    >
                      <Icon className="w-4 h-4 mr-2 text-muted-foreground group-hover:text-primary transition-colors" />
                      {search.label}
                    </Button>
                  )
                })}
              </div>
            </div>

            {/* Feature Cards */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-12 animate-fade-in" style={{ animationDelay: '300ms' }}>
              <div className="p-6 rounded-2xl bg-white/50 dark:bg-slate-900/50 border border-slate-200/50 dark:border-slate-700/50 backdrop-blur-sm">
                <div className="w-10 h-10 rounded-xl bg-blue-100 dark:bg-blue-900/50 flex items-center justify-center mb-4">
                  <Sparkles className="w-5 h-5 text-blue-600 dark:text-blue-400" />
                </div>
                <h3 className="font-semibold mb-2">AI-Powered Answers</h3>
                <p className="text-sm text-muted-foreground">Get intelligent summaries from your institutional documents</p>
              </div>
              <div className="p-6 rounded-2xl bg-white/50 dark:bg-slate-900/50 border border-slate-200/50 dark:border-slate-700/50 backdrop-blur-sm">
                <div className="w-10 h-10 rounded-xl bg-purple-100 dark:bg-purple-900/50 flex items-center justify-center mb-4">
                  <FileText className="w-5 h-5 text-purple-600 dark:text-purple-400" />
                </div>
                <h3 className="font-semibold mb-2">Document Sources</h3>
                <p className="text-sm text-muted-foreground">See exactly which documents contain your information</p>
              </div>
              <div className="p-6 rounded-2xl bg-white/50 dark:bg-slate-900/50 border border-slate-200/50 dark:border-slate-700/50 backdrop-blur-sm">
                <div className="w-10 h-10 rounded-xl bg-green-100 dark:bg-green-900/50 flex items-center justify-center mb-4">
                  <GraduationCap className="w-5 h-5 text-green-600 dark:text-green-400" />
                </div>
                <h3 className="font-semibold mb-2">Bilingual Support</h3>
                <p className="text-sm text-muted-foreground">Search in English or Filipino - we understand both</p>
              </div>
            </div>
          </div>
        )}

        {/* Compact Search Bar when showing results */}
        {hasPerformedSearch && (
          <div className="mb-8 animate-fade-in">
            <div className="relative max-w-3xl mx-auto">
              <div className="relative bg-white dark:bg-slate-900 rounded-xl shadow-lg border border-slate-200/50 dark:border-slate-700/50 overflow-hidden">
                <div className="flex items-center gap-3 p-2">
                  <div className="flex items-center justify-center w-10 h-10 rounded-lg bg-gradient-to-br from-blue-500 to-blue-600 text-white shrink-0 ml-2">
                    <SearchIcon className="w-4 h-4" />
                  </div>
                  <Input
                    placeholder="Search for documents..."
                    value={searchQuery}
                    onChange={(e) => setSearchQuery(e.target.value)}
                    onKeyDown={(e) => {
                      if (e.key === 'Enter') {
                        performSearch();
                      }
                    }}
                    className="flex-1 h-12 text-base border-0 shadow-none focus-visible:ring-0 bg-transparent"
                  />
                  <Button
                    onClick={performSearch}
                    size="default"
                    className="h-10 px-5 rounded-lg bg-gradient-to-r from-blue-600 to-blue-700 hover:from-blue-700 hover:to-blue-800 text-white mr-2"
                  >
                    Search
                  </Button>
                </div>
              </div>
            </div>
          </div>
        )}

        {/* Search Results */}
        {searchQuery && hasPerformedSearch && (
          <div className="animate-fade-in">
            <div className="mb-6 flex items-center justify-between">
              <div>
                <h2 className="text-xl font-semibold flex items-center gap-2">
                  {loading ? (
                    <>
                      <div className="w-5 h-5 border-2 border-primary border-t-transparent rounded-full animate-spin" />
                      <span>Searching...</span>
                    </>
                  ) : (
                    <>
                      <span className="text-2xl font-bold text-primary">{searchResults.documents.length}</span>
                      <span className="text-muted-foreground font-normal">
                        {searchResults.documents.length === 1 ? "result" : "results"} for
                      </span>
                      <span className="text-foreground">"{searchQuery}"</span>
                    </>
                  )}
                </h2>
                {generatedResponse && searchResults.documents.length > 0 && (
                  <p className="text-sm text-muted-foreground mt-1 flex items-center gap-2">
                    <Sparkles className="w-3 h-3" />
                    AI-generated answer based on your documents
                  </p>
                )}
              </div>
              {!loading && searchResults.documents.length > 0 && (
                <Button
                  variant="ghost"
                  size="sm"
                  onClick={() => {
                    setSearchQuery("")
                    setHasPerformedSearch(false)
                    setSearchResults({ documents: [] })
                    setGeneratedResponse(null)
                    setSources([])
                    setRelevantDocumentUrl(undefined)
                  }}
                  className="text-muted-foreground hover:text-foreground"
                >
                  Clear search
                </Button>
              )}
            </div>

            {/* Display generated response if available */}
            <QwenResponseDisplay
              generatedResponse={generatedResponse || ''}
              generationType={generationType}
              sources={sources} // Pass the sources used in the AI response
              relevantDocumentUrl={relevantDocumentUrl} // Pass the relevant document URL
              isLoading={isGenerating} // Always show loading when generating since we're always using semantic search
            />

            {/* Only show the documents tab if we don't have a generated response or if we want to show both */}
            {!generatedResponse ? (
              searchResults.documents.length > 0 && (
                <div className="space-y-4">
                  <div className="flex items-center gap-2 mb-4">
                    <FileText className="w-5 h-5 text-muted-foreground" />
                    <h3 className="font-semibold">Documents</h3>
                  </div>

                  {loading ? (
                    <div className="flex flex-col items-center justify-center py-12">
                      <div className="w-12 h-12 border-4 border-primary border-t-transparent rounded-full animate-spin mb-4"></div>
                      <p className="text-muted-foreground">Finding relevant documents...</p>
                    </div>
                  ) : (
                    <div className="space-y-4">
                      {searchResults.documents.map((doc, index) => {
                        // Check if this document is from enhanced search (has additional properties)
                        const enhancedResult = Array.isArray(searchResults.documents) && searchResults.documents.length > 0 && 'documentId' in searchResults.documents[0];
                        const enhancedDoc = enhancedResult ? (searchResults as any).documents[index] : null;
                        
                        return (
                          <Card 
                            key={`${doc.id}-${index}`} 
                            className="group hover:shadow-xl hover:border-primary/20 transition-all duration-300 cursor-pointer bg-white dark:bg-slate-900 border-slate-200/50 dark:border-slate-700/50 overflow-hidden" 
                            onClick={() => {
                              // Navigate to the document preview page
                              const resultWithUrl = doc as any;
                              if (resultWithUrl.documentUrl && resultWithUrl.documentUrl !== `/repository/preview/undefined` && !resultWithUrl.documentUrl.includes('/repository/preview/undefined')) {
                                router.push(resultWithUrl.documentUrl);
                              }
                              else if (enhancedDoc?.originalDocumentId) {
                                router.push(`/repository/preview/${enhancedDoc.originalDocumentId}`);
                              }
                              else if (enhancedDoc?.documentUrl && enhancedDoc.documentUrl !== `/repository/preview/undefined` && !enhancedDoc.documentUrl.includes('/repository/preview/undefined')) {
                                router.push(enhancedDoc.documentUrl);
                              }
                              else if (doc.colivaraDocumentId) {
                                router.push(`/repository/preview/${doc.colivaraDocumentId}`);
                              }
                              else {
                                router.push(`/repository/preview/${doc.id}`);
                              }
                            }}
                          >
                            <CardHeader className="pb-3">
                              <div className="flex items-start gap-4">
                                <div className="w-12 h-12 bg-gradient-to-br from-blue-500/10 to-purple-500/10 rounded-xl flex items-center justify-center shrink-0 group-hover:from-blue-500/20 group-hover:to-purple-500/20 transition-colors">
                                  <FileText className="w-6 h-6 text-blue-600 dark:text-blue-400" />
                                </div>
                                <div className="flex-1 min-w-0">
                                  <div className="flex items-start justify-between gap-3">
                                    <CardTitle className="text-lg leading-tight group-hover:text-primary transition-colors line-clamp-2">
                                      {cleanDocumentTitle(SuperMapper.getFieldValue(doc, 'title') || (doc as any).title || ((doc as any).document && SuperMapper.getFieldValue((doc as any).document, 'title')) || (doc as any).originalName || "Untitled Document")}
                                    </CardTitle>
                                    <Badge variant="secondary" className="shrink-0 bg-slate-100 dark:bg-slate-800 text-slate-700 dark:text-slate-300">
                                      {SuperMapper.getFieldValue(doc, 'category') || (doc as any).category || (doc as any).type || "Uncategorized"}
                                    </Badge>
                                  </div>
                                  {/* Show enhanced content snippet if available */}
                                  {enhancedDoc?.snippet ? (
                                    <CardDescription className="mt-2 line-clamp-2 text-sm" dangerouslySetInnerHTML={{ __html: enhancedDoc.snippet }} />
                                  ) : (
                                    <CardDescription className="mt-2 line-clamp-2 text-sm">
                                      {(doc as any).snippet || (doc as any).content || (doc as any).text || doc.description || (doc as any).document?.description || (doc as any).summary || "No preview available"}
                                    </CardDescription>
                                  )}
                                </div>
                              </div>
                            </CardHeader>
                            <CardContent className="pt-0 pb-4">
                              <div className="flex items-center justify-between gap-4">
                                <div className="flex flex-wrap gap-1.5">
                                  {(doc.tags || (doc as any).keywords || []).slice(0, 3).map((tag: string, tagIndex: number) => (
                                    <Badge key={tagIndex} variant="outline" className="text-xs font-normal border-slate-200 dark:border-slate-700">
                                      {tag}
                                    </Badge>
                                  ))}
                                  {((doc.tags || (doc as any).keywords || []).length > 3) && (
                                    <Badge variant="outline" className="text-xs font-normal border-slate-200 dark:border-slate-700">
                                      +{(doc.tags || (doc as any).keywords || []).length - 3} more
                                    </Badge>
                                  )}
                                </div>
                                
                                {/* Metadata and Preview Button */}
                                <div className="flex items-center gap-3 shrink-0">
                                  {enhancedDoc?.confidenceScore && (
                                    <div className="flex items-center gap-1 text-xs text-muted-foreground">
                                      <Star className="w-3 h-3 text-amber-500" />
                                      <span>{(enhancedDoc.confidenceScore * 100).toFixed(0)}%</span>
                                    </div>
                                  )}
                                  {enhancedDoc?.pageNumbers && enhancedDoc.pageNumbers.length > 0 && (
                                    <div className="text-xs text-muted-foreground">
                                      p. {enhancedDoc.pageNumbers.slice(0, 3).join(', ')}
                                    </div>
                                  )}
                                  <Button 
                                    variant="ghost" 
                                    size="sm" 
                                    className="h-8 px-3 text-primary opacity-0 group-hover:opacity-100 transition-opacity"
                                    onClick={(e) => {
                                      e.stopPropagation();
                                      const resultWithUrl = doc as any;
                                      if (resultWithUrl.documentUrl && resultWithUrl.documentUrl !== `/repository/preview/undefined` && !resultWithUrl.documentUrl.includes('/repository/preview/undefined')) {
                                        router.push(resultWithUrl.documentUrl);
                                      }
                                      else if (enhancedDoc?.originalDocumentId) {
                                        router.push(`/repository/preview/${enhancedDoc.originalDocumentId}`);
                                      }
                                      else if (enhancedDoc?.documentUrl && enhancedDoc.documentUrl !== `/repository/preview/undefined` && !enhancedDoc.documentUrl.includes('/repository/preview/undefined')) {
                                        router.push(enhancedDoc.documentUrl);
                                      }
                                      else if (doc.colivaraDocumentId) {
                                        router.push(`/repository/preview/${doc.colivaraDocumentId}`);
                                      }
                                      else {
                                        router.push(`/repository/preview/${doc.id}`);
                                      }
                                    }}
                                  >
                                    <Eye className="w-4 h-4 mr-1" />
                                    View
                                  </Button>
                                </div>
                              </div>
                              {/* Show visual content if available */}
                              {enhancedDoc?.visualContent && (
                                <div className="mt-4 pt-4 border-t border-slate-100 dark:border-slate-800">
                                  <div className="text-xs font-medium text-muted-foreground mb-2 flex items-center gap-1">
                                    <Eye className="w-3 h-3" />
                                    Preview from document
                                  </div>
                                  <img
                                    src={`data:image/png;base64,${enhancedDoc.visualContent}`}
                                    alt="Evidence from document"
                                    className="max-w-full max-h-40 object-contain border rounded-lg shadow-sm"
                                  />
                                </div>
                              )}
                            </CardContent>
                          </Card>
                        );
                      })}
                    </div>
                  )}
                </div>
              )
            ) : null}

            {!loading && searchResults.documents.length === 0 && (
              <Card className="border-dashed border-2">
                <CardContent className="py-16 text-center">
                  <div className="w-20 h-20 mx-auto mb-6 rounded-full bg-slate-100 dark:bg-slate-800 flex items-center justify-center">
                    <SearchIcon className="w-10 h-10 text-muted-foreground/50" />
                  </div>
                  <h3 className="text-xl font-semibold mb-2">No results found</h3>
                  <p className="text-muted-foreground mb-6 max-w-md mx-auto">
                    We couldn't find any documents matching "{searchQuery}". Try different keywords or browse the repository.
                  </p>
                  <div className="flex flex-col sm:flex-row items-center justify-center gap-3">
                    <Button
                      variant="outline"
                      onClick={() => {
                        setSearchQuery("")
                        setHasPerformedSearch(false)
                      }}
                    >
                      Clear search
                    </Button>
                    <Button
                      onClick={() => router.push('/repository')}
                    >
                      Browse Repository
                    </Button>
                  </div>
                </CardContent>
              </Card>
            )}
          </div>
        )}
      </main>
    </div>
  )
}
